.brand-header{
    text-align: center;
    color: white;
    
}
.first-header{
    display: inline-flex;
    text-align: center;
}
.scooter-img img{
    width: 70px;
}
hr{
    height: 0px;
    width: 53px;
    margin-top: 22%;
    margin-right: 5px;
    margin-left: 5px;
    background: antiquewhite;
    margin-bottom: 0px;
}
.second-header{
    color: white;
    text-align: center;
    font-family: cursive;
    margin-bottom: 2%;
}
h1{
    line-height: .8 !important;
}
.container{
    background: transparent;
    font-size: 100%;
    font-weight: normal;
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    vertical-align: top;
}
.centered{
    position: absolute;
    top: 89%;
    left: 50%;
    transform: translate(-50%, -80%);
    color: black;
}
@media (max-width: 550px){
    h1{
        font-size: 14px !important;
    }
    h2{
        font-size: 12px !important;
    }
}