header{
    width: 100%;
    /* background: rgba(0,0,0,0.3); */
}
Form{
    float: right;
}
a{
    color: #808080;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0 1rem;
    height: 100%;
    cursor: pointer;
    font-weight: bold;
    background: transparent;
    text-align: center;
    width: min-content;
}
a:focus, a:hover {
	color: #fbcf6b !important;
    background: black;
};
Link:active{
    color: #fbcf6b !important;
    background: black;
}
.active{
    color: #fbcf6b !important;
    background: black;
}
Nav{
    background: rgba(0,0,0,0.3);
    height: 55px;
    display: flex;
    justify-content: space-between;
    width:100%;
    z-index: 12;
}
button{
    background-color: white !important;
    margin-left: 2%;
}
.nav{
    line-height: 2rem !important;
    margin-bottom: 3%;
}
.navbar{
    padding: 0px !important;
}
.container, #responsive-navbar-nav, .navbar-nav{
    height: 3.5em !important;
}
